"use client";

// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";

// Utils
import { EmailValidator } from "@/utilities/regex";

// API
import { Forgotpassword } from "@/api/clientsideaction/buyerapi";

// Component
import { useTranslation } from "react-i18next";

export default function ForgotForm() {
  const { t } = useTranslation();

  const [email, setEmail] = useState("");
  const [validecheck, setValideCheck] = useState(false);
  const [mailError, setMailError] = useState("");
  const [status, setStatus] = useState(false);
  const [checkEmail, setCheckEmail] = useState(false);
  const [loader, setLoader] = useState(false);
  const [apistatus, setApiStatus] = useState();
  const [triger, setTriger] = useState(0);
  const [emailErrorShow, setEmailErrorShow] = useState(false);

  useEffect(() => {
    if (apistatus == 1) {
      const Timeout = () => {
        setStatus(false);
        setValideCheck(false);
        setCheckEmail(false);
        setEmail("");
      };
      setTimeout(Timeout, 3000);
    }
  }, [apistatus, triger]);

  useEffect(() => {
    setStatus(false);
    if (validecheck) {
      validate();
    }
    setStatus(false);
    if (validate()) {
      setCheckEmail(true);
    } else {
      setCheckEmail(false);
    }
  }, [email]);

  const handleSubmit = () => {
    setValideCheck(true);
    if (validate()) {
      setLoader(true);
      Forgotpassword(
        email,
        setStatus,
        setMailError,
        setLoader,
        setApiStatus,
        triger,
        setTriger,
        t
      );
    }
  };

  const validate = () => {
    let ValidateObj = {
      email: true,
    };
    if (email == "") {
      setMailError("");
      ValidateObj.email = false;
    } else {
      if (EmailValidator(email)) {
        setMailError("");
        ValidateObj.email = true;
      } else {
        setMailError(t("validationMessage.incorrectEmail"));
        ValidateObj.email = false;
      }
    }

    if (ValidateObj.email == true) {
      return true;
    } else {
      return false;
    }
  };

  const handleEnterEmailClick = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleSubmit();
    }
  };

  return (
    <div className="w-full px-4 py-4 bg-blue-100 lg:px-32 sm:py-10 md:w-1/2">
      <Link
        prefetch={false}
        className="flex justify-end mb-8 text-sm font-semibold text-link"
        href="/buyer-login"
      >
        {t("forgotpassword.Back")}
      </Link>
      <div className="mb-8">
        <h4 className="mb-1 text-2xl font-black text-black-300">
          {t("forgotpassword.FForgotPwd")}?
        </h4>
        <p className="text-base font-normal text-black-300">
          {t("forgotpassword.NeverMind")}
        </p>
      </div>
      <div className="flex flex-col mb-8 space-y-6">
        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <input
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value.trimStart())}
            onBlur={(e) => setEmailErrorShow(true)}
            onKeyDown={(e) => handleEnterEmailClick(e)}
            onFocus={(e) => setEmailErrorShow(false)}
            className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400 ${
              mailError && email !== "" && emailErrorShow && "border-valid"
            }`}
            id="exampleFormControlInput1"
            placeholder={t("signin.Examplelabel")}
          />
          <label
            htmlFor="exampleFormControlInput1"
            className={
              email == ""
                ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                    validecheck == true && mailError != "" && "text-red-invalid"
                  }`
                : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                    mailError &&
                    email !== "" &&
                    emailErrorShow &&
                    "text-red-invalid"
                  }`
            }
          >
            {t("forgotpassword.YEmail")}
          </label>
          {mailError && email !== "" && emailErrorShow && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {mailError}
            </p>
          )}
          {status == 1 && (
            <p className="mt-1 text-sm font-normal text-activegreen">
              {t("forgotpassword.ThankYou")}
            </p>
          )}
        </div>
      </div>
      <div>
        <button
          disabled={checkEmail == false}
          className={`w-full text-white font-bold text-base btn bg-boldblack hover:bg-zinc-800 justify-center items-center flex bg-boldblack h-11 rounded ${
            checkEmail == true ? "opacity-100" : "opacity-15"
          }`}
          onClick={handleSubmit}
        >
          {" "}
          {loader ? (
            <svg
              aria-hidden="true"
              role="status"
              className="inline w-5 h-5 text-white me-3 animate-spin"
              viewBox="0 0 100 101"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                fill="#E5E7EB"
              />
              <path
                d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                fill="currentColor"
              />
            </svg>
          ) : (
            t("forgotpassword.Submit")
          )}
        </button>
      </div>
    </div>
  );
}
